@import url(https://fonts.googleapis.com/css?family=Roboto);


nav#menu {
  background: #158a88;
  text-align: center;
  height: 50px;
  width: 100%;
  top: 0px;
  margin-top: 0px !important;
  position: fixed !important;
}

nav#menu ul {
  padding: 0;
  background: white;
  margin: 0 auto;
  display: inline-block;
  height: 50px;
}

nav#menu ul li {
  float: left;
  padding: 0 20px;
  background: #17bebb;
  list-style: none;
  margin-right: 5px;
  line-height: 50px;
  height: 50px;
  
  /*
   * This element will get an alternative style
   * once we apply the "scrolled" class.
   * We also want a nice animation so we can
   * simply set CSS3 transitions for this.
   */
  transition: margin 0.1s ease-in-out;
}

nav#menu ul li:nth-child(1) {
  margin-left: 5px;
}

nav#menu ul li.active {
  background: #17bebb;
}

nav#menu ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.3em;
}

nav#menu h1#sitename {
  font-size: 1.2em;
  line-height: 50px;
  margin: 0;
  position: absolute;
  left: 0.5em;
  color: white;
  opacity: 0;
  
  /*
   * Same thing here as with the menu items.
   * This element will be animated into its
   * alt. state with the transition property
   */
  transition: opacity 0.1s ease-in-out;
}


/*********************************************
 * Once the 'scrolled' class is toggled 
 * we can set any alternative styling that we
 * want for the menu bar 
 *********************************************/

nav#menu.scrolled {
  margin: 0;
  position: fixed;
  top: 0;
}

nav#menu.scrolled ul li {
  margin: 0;
}

nav#menu.scrolled h1#sitename {
  opacity: 1;
}

@media screen and (max-width: 1024px) {
    /*
    nav#menu h1#sitename {
        font-size: .2em;
        line-height: .5em;
    }
*/
    nav#menu ul li:nth-child(1) {
        margin-left: 3px;
    }

    nav#menu {
        margin-top: 30px;
        height: 30px;
        width: 100%;
      }
      
      nav#menu ul {
        height: 30px;
      }
      
      nav#menu ul li {
        padding: 0 8px;
        margin-right: 3px;
        line-height: 30px;
        font-size: .3em;
        height: 30px;
      }
}